<script>
import "sweetalert2/dist/sweetalert2.css";
import HtmlEditor from "@/components/Inputs/HtmlEditor";
import DropzoneFileUpload from "@/components/Inputs/DropzoneFileUpload";
import flatPicker from "vue-flatpickr-component";
import "flatpickr/dist/flatpickr.css";
import { Table, TableColumn, Select, Option } from "element-ui";
import { BasePagination, BaseInput, BaseSwitch } from "@/components";
import clientPaginationMixin from "@/components/clientPaginationMixin";
import swal from "sweetalert2";
import users from "./users2";
import axios from "axios";
import moment from "moment";
import cabang from "../../cabang";
import listPeriode from "@/views/Pages/listPeriode.js";

export default {
  mixins: [clientPaginationMixin],
  components: {
    listPeriode,
    HtmlEditor,
    BasePagination,
    BaseInput,
    BaseSwitch,
    flatPicker,
    DropzoneFileUpload,
    [Select.name]: Select,
    [Option.name]: Option,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      isDataParamNull: true,
      listIL: [],
      tempIL: {},
      periode: "",
      isUpdate: false,
      matiksHolder: "Pilih Matriks RPL",
      tempUpdateRow: {},
      petaButton: "Tampilkan Peta",
      listPeriode,
      cabang,
      selects: {
        unitId: null
      },
      tableData3: {},
      detailParam: [],
      paramTitik: [
        ["", "", "", "", ""],
        ["", "", "", "", ""],
        ["", "", "", "", ""]
      ],
      jumlahTempParamTitik: 1,
      tempParamTitik: [],
      jumlahTitik: 1,
      petaLink: "",
      isPeta: false,
      loopUpaya: {
        data: [],
        datahasil: [],
        datacapai: []
      },
      ketercapaian: [],
      testHTML: "<p>test</p>",
      detailUpaya: [],
      jumlahUpaya: 0,
      optionsMatriks: {},
      uk_id: null,
      totalUpaya: 0,
      formPelaporanRPL: {
        periode: "",
        no: "",
        date: null,
        matriksrpl_id: "",
        dataparam: "",
        datacapai: "",
        datahasil: "",
        data: ""
      },
      dates: {
        simple: new Date("2020-10-10"),
        range: "2019-04-19 to 2019-04-24"
      },
      isTable: true,
      isForm: false,
      isDetail: false,
      propsToSearch: ["name", "email", "age"],
      tableColumns3: [
        {
          align: "center",
          prop: "param",
          label: "Parameter",
          minWidth: 150
        },
        {
          prop: "mutu",
          label: "Baku Mutu",
          minWidth: 150
        },
        {
          prop: "satuan",
          label: "Satuan",
          minWidth: 150
        }
      ],
      tableColumns2: [
        {
          align: "center",
          prop: "IL_DATE",
          label: "Upaya Pemantauan lingkungan",
          minWidth: 150
        },
        {
          prop: "UNITKERJA_ID",
          label: "Pelaksanaan RPL",
          minWidth: 150
        },
        {
          prop: "IL_NOMORIZIN",
          label: "Hasil yang Dicapai",
          minWidth: 150
        },
        {
          prop: "MRPL_TAHAPAN",
          label: "Ketercapaian",
          minWidth: 150
        }
      ],
      tableColumns: [
        {
          align: "center",
          prop: "IL_DATE",
          label: "Tanggal",
          minWidth: 150
        },
        {
          prop: "UK_NAME",
          label: "Unit Kerja",
          minWidth: 150
        },
        {
          prop: "DATAPERIODE",
          label: "Periode",
          minWidth: 150
        },
        {
          prop: "IL_NAME",
          label: "Izin Lingkungan",
          minWidth: 150
        },
        {
          prop: "MRPL_KEGIATAN",
          label: "Matriks",
          minWidth: 150
        }
      ],
      tableData: [],
      tableData2: [],
      selectedRows: []
    };
  },
  created() {
    this.getTable(this.$store.getters.getUKId);
    if (!this.$store.getters.isAdmin) {
      this.uk_id = this.$store.getters.getUKId;
      this.getIzinLingkungan();
    }
    // this.getListPeriode();
  },
  watch: {
    jumlahTitik() {
      if (!this.isUpdate) {
        let temp = [];
        for (let i = 0; i < this.jumlahTitik; i++) {
          let tempParam = [];
          for (let j = 0; j < this.jumlahTempParamTitik; j++) {
            tempParam.push("");
          }
          temp.push(tempParam);
        }
        this.paramTitik = [];
        this.paramTitik = temp;
      }
      if (this.isUpdate) {
        this.paramTitik = [];
        this.paramTitik = JSON.parse(this.tempUpdateRow.DATAPARAM);
        let gap = this.jumlahTitik - this.paramTitik.length;
        let temp = [];
        for (let i = 0; i < gap; i++) {
          let tempParam = [];
          for (let j = 0; j < this.jumlahTempParamTitik; j++) {
            tempParam.push("");
          }
          this.paramTitik.push(tempParam);
        }
      }
    }
  },
  methods: {
    dataShift(data) {
      let tempData = [];
      for (let i = 1; i < data.length; i++) {
        tempData.push(data[i]);
      }
      return tempData;
    },
    changePeriode(value) {
      if (value == null) return (this.tableData = this.tableDataOri);
      let tempTable = this.tableDataOri.filter(
        item => item.DATAPERIODE == value
      );
      this.tableData = tempTable;
    },
    getListPeriode() {
      axios
        .get("http://45.127.133.75:3000/api/iems/list-periode")
        .then(res => {
          console.log(res.data);
          let tempData = [];
          res.data.forEach(item => {
            tempData.push({
              name: item.PERIODE,
              value: item.PERIODE
            });
          });
          console.log(tempData);
          this.listPeriode = tempData;
        })
        .catch(err => {
          console.log(err);
        });
    },
    submitFormUpdate() {
      let updateData = this.formPelaporanRPL;
      updateData.data = JSON.stringify(this.loopUpaya.data);
      updateData.datacapai = JSON.stringify(this.loopUpaya.datacapai);
      updateData.datahasil = JSON.stringify(this.loopUpaya.datahasil);
      updateData.dataparam = JSON.stringify(this.paramTitik);
      axios
        .put(
          "http://45.127.133.75:3000/api/iems/rpl-report/" +
            this.tempUpdateRow.DATARPL_ID,
          updateData
        )
        .then(res => {
          swal
            .fire({
              title: `Success`,
              text: "Update Form berhasil tersimpan!",
              buttonsStyling: false,
              confirmButtonClass: "btn btn-success",
              icon: "success"
            })
            .then(result => {
              this.isUpdate = false;
              this.getTable(this.uk_id);
              this.$router.push("daftar");
            });
        })
        .catch(err => {
          console.log(err);
          swal.fire({
            title: `Warning`,
            text: "Update Form tidak tersimpan!!!" + err,
            buttonsStyling: false,
            confirmButtonClass: "btn btn-warning",
            icon: "warning"
          });
        });
    },
    swalWarning(message) {
      swal.fire({
        title: `Warning`,
        text: message,
        buttonsStyling: false,
        confirmButtonClass: "btn btn-warning",
        icon: "warning"
      });
    },
    swalError(message) {
      swal.fire({
        title: `Error!`,
        text: message,
        buttonsStyling: false,
        confirmButtonClass: "btn btn-danger",
        icon: "error"
      });
    },
    deleteData(id) {
      swal
        .fire({
          title: "Apakah anda yakin untuk menghapus?",
          showCancelButton: true,
          confirmButtonText: `Hapus`,
          cancelButtonText: `Kembali`,
          confirmButtonColor: "#d33",
          cancelButtonColor: "#3085d6",
          icon: "question"
        })
        .then(result => {
          /* Read more about isConfirmed, isDenied below */
          if (result.isConfirmed) {
            axios
              .delete("http://45.127.133.75:3000/api/iems/mrpl-rep/" + id)
              .then(res => {
                swal
                  .fire({
                    title: `Success`,
                    text: "Berhasil terhapus!",
                    buttonsStyling: false,
                    confirmButtonClass: "btn btn-success",
                    icon: "success"
                  })
                  .then(result => {
                    this.getTable(this.$store.getters.getUKId);
                    if (!this.$store.getters.isAdmin) {
                      this.uk_id = this.$store.getters.getUKId;
                      this.getIzinLingkungan();
                    }
                    this.$router.push("daftar");
                  });
              })
              .catch(err => {
                console.log(err);
                this.swalError("Terjadi Kesalahan " + err);
              });
          }
        });
    },
    toUpdateForm() {
      this.isUpdate = true;
      this.uk_id = this.tempUpdateRow.UNITKERJA_ID;
      this.tempIL.label = this.tempUpdateRow.IL_NAME;
      this.getIzinLingkungan();
      this.formPelaporanRPL.matriksrpl_id = this.tempUpdateRow.MATRIKSRPL_ID;
      this.matiksHolder = this.tempUpdateRow.MRPL_KEGIATAN;
      this.datarpl_id = this.tempUpdateRow.DATARPL_ID;
      this.il_id = this.tempUpdateRow.IL_ID;
      this.detailUpaya = JSON.parse(this.tempUpdateRow.MRPL_UPENGELOLAAN);
      this.jumlahUpaya = this.detailUpaya.length;
      this.loopUpaya.data = JSON.parse(this.tempUpdateRow.DATA);
      this.loopUpaya.datacapai = JSON.parse(this.tempUpdateRow.DATACAPAI);
      this.loopUpaya.datahasil = JSON.parse(this.tempUpdateRow.DATAHASIL);
      this.tempIL.label;

      this.formPelaporanRPL.no = this.tempUpdateRow.IL_NOMORIZIN;
      this.formPelaporanRPL.periode = this.tempUpdateRow.DATAPERIODE;
      this.formPelaporanRPL.date = this.tempUpdateRow.IL_DATE;

      this.tempParamTitik = [];
      let tempLabel = JSON.parse(this.tempUpdateRow.MRPL_PARAMPEMANTAUAN);
      // let tempLabel = res.data[0].MRPL_PARAMPEMANTAUAN.split("&%");
      // tempLabel.pop();

      this.jumlahTempParamTitik = tempLabel.length;
      for (let i = 0; i < tempLabel.length; i++) {
        this.tempParamTitik.push(
          tempLabel[i][0] + " (" + tempLabel[i][1] + " " + tempLabel[i][2] + ")"
        );
      }

      this.paramTitik = JSON.parse(this.tempUpdateRow.DATAPARAM);
      this.jumlahTitik = this.paramTitik.length;
      axios
        .get("http://45.127.133.75:3000/api/iems/rpl/9999?ilid=" + this.il_id)
        .then(res => {
          let tempMatriks = res.data;
          let listMatriks = [];
          tempMatriks.forEach(element => {
            listMatriks.push({
              label: element.MRPL_KEGIATAN,
              value: element.MATRIKSRPL_ID
            });
          });
          this.optionsMatriks = listMatriks;
        });

      this.$router.push("form");
    },
    showPeta() {
      if (this.isPeta === false) {
        this.isPeta = true;
        this.petaButton = "Sembunyikan Peta";
      } else {
        this.isPeta = false;
        this.petaButton = "Tampilkan Peta";
      }
    },
    titikCheck() {
      return this.jumlahTitik > 0;
    },
    nullHandler(n) {
      if (n === 0 || n === null || n === undefined) {
        return 1;
      }
      return parseInt(n);
    },
    encodeDetail(data) {
      // data = data.split("&%");
      // data.pop();
      return JSON.parse(data);
    },
    toDetail(row, col, event) {
      this.petaLink = row.IL_MAPRPL_LINK;
      this.tempUpdateRow = row;
      let tempTable2 = [];
      let tempDataUpaya = this.encodeDetail(row.MRPL_UPENGELOLAAN);

      let tempData = this.encodeDetail(row.DATA);

      let tempDataHasil = this.encodeDetail(row.DATAHASIL);
      let tempDataCapai = this.encodeDetail(row.DATACAPAI);
      for (let i = 0; i < tempDataUpaya.length; i++) {
        if (tempDataCapai[i] === true) {
          tempDataCapai[i] = "Tercapai";
        } else {
          tempDataCapai[i] = "Tidak Tercapai";
        }
        tempTable2.push({
          dataupaya: tempDataUpaya[i],
          data: tempData[i],
          datahasil: tempDataHasil[i],
          datacapai: tempDataCapai[i]
        });
      }

      this.tableData2 = tempTable2;

      let tempTable3 = [];
      let tempParamTable = JSON.parse(row.MRPL_PARAMPEMANTAUAN);

      let tempRow = JSON.parse(row.DATAPARAM);
      this.isDataParamNull = false;
      if (tempRow[0][0] == "") {
        this.isDataParamNull = true;
      }
      this.tableColumns3 = [
        {
          align: "center",
          prop: "param",
          label: "Parameter",
          minWidth: 150
        },
        {
          prop: "mutu",
          label: "Baku Mutu",
          minWidth: 150
        },
        {
          prop: "satuan",
          label: "Satuan",
          minWidth: 150
        }
      ];
      for (let i = 0; i < tempParamTable.length; i++) {
        tempTable3.push({
          param: tempParamTable[i][0],
          mutu: tempParamTable[i][1],
          satuan: tempParamTable[i][2]
        });
        for (let j = 0; j < tempRow.length; j++) {
          tempTable3[i][tempRow[j][0]] = tempRow[j][i + 1];
        }
      }
      for (let k = 0; k < tempRow.length; k++) {
        this.tableColumns3.push({
          prop: tempRow[k][0],
          label: tempRow[k][0],
          minWidth: 150
        });
      }

      this.tableData3 = tempTable3;
      this.$router.push("detail");
    },
    toFormatString(data) {
      // let tempData = [];
      // for (let i = 0; i < data.length; i++) {
      //   tempData = tempData + data[i] + "&%";
      // }
      return JSON.stringify(data);
    },
    submitForm() {
      let tempCapai = this.loopUpaya.datacapai;
      for (let i = 0; i < this.jumlahUpaya; i++) {
        if (tempCapai[i] !== true) {
          tempCapai[i] = false;
        }
      }
      this.formPelaporanRPL.data = JSON.stringify(this.loopUpaya.data);
      this.formPelaporanRPL.datahasil = JSON.stringify(
        this.loopUpaya.datahasil
      );
      this.formPelaporanRPL.datacapai = JSON.stringify(
        this.loopUpaya.datacapai
      );
      let arrayParam = this.paramTitik;
      this.formPelaporanRPL.dataparam = JSON.stringify(arrayParam);

      axios
        .post(
          "http://45.127.133.75:3000/api/iems/rpl-report-new/",
          this.formPelaporanRPL
        )
        .then(res => {
          swal
            .fire({
              title: `Success`,
              text: "Form berhasil tersimpan!",
              buttonsStyling: false,
              confirmButtonClass: "btn btn-success",
              icon: "success"
            })
            .then(result => {
              this.isUpdate = false;
              this.getTable(this.uk_id);
              this.$router.push("daftar");
            });
        })
        .catch(err => {
          console.log(err);
          swal.fire({
            title: `Warning`,
            text: "Form tidak tersimpan!!!",
            buttonsStyling: false,
            confirmButtonClass: "btn btn-warning",
            icon: "warning"
          });
        });
    },
    getTable(data) {
      axios
        .get("http://45.127.133.75:3000/api/iems/rpl-report/" + data)
        .then(res => {
          console.log(res.data);
          res.data.forEach(el => {
            let tempNilai = JSON.parse(el.DATACAPAI);
            let tempTercapai = tempNilai.filter(item => item == true);
            el.IL_DATE = moment.unix(el.IL_DATE).format("L");
            el.NILAI_PRPL = [tempTercapai.length, tempNilai.length];
          });

          this.tableData = res.data;
          this.tableDataOri = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    getMatriks() {
      axios
        .get(
          "http://45.127.133.75:3000/api/iems/rpl-matriks/" +
            this.formPelaporanRPL.matriksrpl_id
        )
        .then(res => {
          let tempUp = res.data[0].MRPL_UPENGELOLAAN;
          this.detailUpaya = JSON.parse(tempUp);
          // this.detailUpaya = tempUp.split("&%");
          // this.detailUpaya.pop();
          this.jumlahUpaya = this.detailUpaya.length;
          this.tempParamTitik = [];
          let tempLabel = JSON.parse(res.data[0].MRPL_PARAMPEMANTAUAN);
          // let tempLabel = res.data[0].MRPL_PARAMPEMANTAUAN.split("&%");
          // tempLabel.pop();

          this.jumlahTempParamTitik = tempLabel.length;
          for (let i = 0; i < tempLabel.length; i++) {
            this.tempParamTitik.push(
              tempLabel[i][0] +
                " (" +
                tempLabel[i][1] +
                " " +
                tempLabel[i][2] +
                ")"
            );
          }

          // this.loopUpaya.datacapai.forEach((data) => {
          //   data = false;
          // });
          // for (let i = 0; i < this.detailUpaya.length; i++) {
          //   const a = this.detailUpaya[i];
          //   this.detailUpaya[i] = a.replace("&%", "");
          // }
        });
    },
    getIzinLingkungan() {
      axios
        .get("http://45.127.133.75:3000/api/iems/il/" + this.uk_id)
        .then(res => {
          console.log(res.data);
          let tempDataArray = [];
          res.data.forEach(element => {
            tempDataArray.push({
              label: element.IL_NAME,
              value: {
                id: element.IL_ID,
                no_izin: element.IL_NOMORIZIN,
                date_izin: element.IL_DATE
              }
            });
          });
          this.listIL = tempDataArray;
          console.log(this.listIL);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getMatriksDetail() {
      this.formPelaporanRPL.no = this.tempIL.label.no_izin;
      this.formPelaporanRPL.date = moment
        .unix(this.tempIL.label.date_izin)
        .format("L");
      axios
        .get(
          "http://45.127.133.75:3000/api/iems/rpl/9999?ilid=" +
            this.tempIL.label.id
        )
        .then(res => {
          let tempMatriks = res.data;
          let listMatriks = [];
          tempMatriks.forEach(element => {
            listMatriks.push({
              label: element.MRPL_KEGIATAN,
              value: element.MATRIKSRPL_ID
            });
          });
          this.optionsMatriks = listMatriks;
        });
    },
    getILID() {
      axios
        .get("http://45.127.133.75:3000/api/iems/il-latest/" + this.uk_id)
        .then(res => {
          if (!res.data[0]) {
            swal
              .fire({
                title: `Unit Kerja ini tidak memiliki Izin Lingungan`,
                text: "Silakan isi Izin Lingungan Terlebuh Dahulu!",
                buttonsStyling: false,
                showCancelButton: true,
                confirmButtonText: "Ke Form Izin Lingkungan",
                cancelButtonText: "Kembali",
                confirmButtonClass: "btn btn-success",
                cancelButtonClass: "btn btn-danger",
                icon: "error"
              })
              .then(result => {
                /* Read more about isConfirmed, isDenied below */
                if (result.isConfirmed) {
                  this.$router.push("/iems/izin-lingkungan/form");
                }
              });
            return;
          }
          this.formPelaporanRPL.no = res.data[0].IL_NOMORIZIN;
          this.formPelaporanRPL.date = moment
            .unix(res.data[0].IL_DATE)
            .format("L");
          axios
            .get(
              "http://45.127.133.75:3000/api/iems/rpl-matriks-il/" +
                res.data[0].IL_ID
            )
            .then(resp => {
              let tempOpt = [];
              for (let i = 0; i < resp.data.length; i++) {
                tempOpt.push({
                  value: resp.data[i].MATRIKSRPL_ID,
                  label: resp.data[i].MRPL_KEGIATAN
                });
              }
              this.optionsMatriks = tempOpt;
            });
          this.formPelaporanRPL.matriksrpl_id = res.data[0].MATRIKSRPL_ID;
        });
    },
    showDetail() {
      this.isForm = false;
      this.isTable = false;
      this.isDetail = true;
    },
    showForm() {
      this.getIzinLingkungan();
      this.$router.push("form");
    },
    hideForm() {
      this.selects.unitId = "";
      this.periode = "";
      this.$router.push("daftar");
      if (this.$store.getters.isAdmin) {
        this.uk_id = null;
        this.getTable(9999);
      }
    }
  }
};
</script>
